// New Responsive version of isolatr
// TODO/Known bugishness
// 1) maybe think of a better way to handle the plugins that get pulled into isolatr so 
//    we don't have to put the content back every time the panel opens/closes (pass
//    values to hidden fields in plugin code?)
// 2) update the context checking to use the same format as panelizr (use getters & setters, etc.)

; define('core/ef_plugins/isolatr',["jquery", "core/global/helpers", "core/modules/breakdance", "underscore", "handlebars", "libs/amplify.core"], function(jQuery, helpers, breakdance) {
    (function($, window, document, undefined) {
 
        var pluginName = 'isolatr',
            defaults = {
                mainSelector: ".isolatr-main", // OPTIONAL OR USE DEFAULT selector to use to get the "main" page 
                openOnInit: false, //OPTIONAL do you want to open the plugin on init?
                panelName: "", // REQUIRED unique ID to use to identify this isolatr panel (no spaces or special characters!)
                loadTarget: "", // REQUIRED selector for element to load into panel
                hideElement: "", // OPTIONAL selector for element(s) to hide on page
                cancelText: "Cancel", // OPTIONAL - cancel button text
                applyText: "Apply", // OPTIONAL - apply button text
                header: "", // REQUIRED - header text in panel
                contextSwitch: 568, // OPTIONAL - width at which to stop using panelizr
                placeholderElementClass: "isolatr-placeholder", // OPTIONAL config of placeholder element used for inline panels
                openCallback: function() {}, // OPTIONAL callback to fire after open
                applyCallback: function() {}, // OPTIONAL callback to fire when apply button is clicked
                desktopApplyElement: null, //OPTIONAL node to attach apply call to
                applyOnEnter: false, //OPTIONAL apply on enter 
                applyOnChange: null, //OPTIONAL apply the apply when the field with this selector changes
                desktopCancelBtn: null, //OPTIONAL node to attach cancel call to
                cancelCallback: function() {}, // OPTIONAL callback to fire when cancel button is clicked
                closeOutsideTarget: null, // OPTIONAL do you want to close the panel when someone clicks outside of it?  Then pass a selector
                closeOutsideFn: null //OPTIONAL store the click outside handler for easy removal
            };

        // The actual plugin constructor 
        function Isolatr(element, options) {
            // grab the element the plugin was called on
            this.element = element;

            // also save the element as a jquery object
            this.$element = $(element);

            // get per-element inline configuration settings
            this.metadata = this.$element.data('isolatr-options');

            // merge all the configs
            this.options = $.extend({}, defaults, options, this.metadata);

            // save the defaults
            this._defaults = defaults;
            this._name = pluginName;

            // set open flag
            this.isOpen = false;

            // get the main element
            this.$main = $(this.options.mainSelector);

            // other vars to be used later
            this.$placeholder = this.$loadTarget = this.$applyButton = this.$cancelButton = $();

            // call init function 
            this.init();
            if (!$('html').hasClass('lt-ie9')) {
                this._resizeListen();
            }
        }

        // Define this sucker
        Isolatr.prototype = {
            init: function() {
                var plugin = this;

                // set current context
                plugin.currentContext = plugin._getContext();

                if (plugin.currentContext === "MOBILE") {
                    // setup panel
                    plugin._mobileSetup();

                    // setup listeners
                    plugin._clickListen();
                    plugin._historyListen();
                } else {
                    plugin._clickListen();
                }
            },

            _resizeListen: function() {
                var plugin = this;

                var resetAndInit = _.debounce(function() {
                    if (plugin._getContext() !== plugin.currentContext) {

                        // reset everything
                        plugin.close();
                        plugin._reset();

                        // set new context value
                        plugin.currentContext = plugin._getContext();

                        // re-initialize
                        plugin.init();
                    }
                }, 300);

                // register breakpoint with enquire js
                breakdance.register(plugin.options.contextSwitch, resetAndInit, resetAndInit);

            },

            _getContext: function() {
                var plugin = this,
                    bp = document.documentElement.clientWidth;

                return bp < plugin.options.contextSwitch ? "MOBILE" : "DESKTOP";
            },

            _createPanel: function() {
                var plugin = this,
                    panelTemplate,
                    context,
                    template,
                    html;

                panelTemplate = '<div class="panel isolatr" id="{{panelName}}"> \
                                <header class="panel-header panel-header-dualaction"> \
                                    <a href="#" class="panel-header-secondary-button">{{cancelButtonText}}</a> \
                                    <h1> {{headerText}} </h1> \
                                    <a href="#" class="panel-header-primary-button">{{applyButtonText}}</a> \
                                </header> \
                                <div class="isolatr-body panel-body"></div> \
                             </div>';

                // setup obj of values to pass to template
                context = {
                    panelName: plugin.options.panelName,
                    cancelButtonText: plugin.options.cancelText,
                    applyButtonText: plugin.options.applyText,
                    headerText: plugin.options.header
                };

                // render handlebars template
                template = Handlebars.compile(panelTemplate);
                html = template(context);
                plugin.$panel = $(html).hide();

                // save refs
                plugin.$panelBody = plugin.$panel.find('.panel-body');
                plugin.$cancelButton = plugin.$panel.find('header .panel-header-secondary-button');
                plugin.$applyButton = plugin.$panel.find('header .panel-header-primary-button');
            },

            _mobileSetup: function() {
                var plugin = this;

                // create the isolatr panel
                this._createPanel();

                // append the panel into the doc
                plugin.$main.after(plugin.$panel);

                // add listeners
                plugin._cancelClickListen();
                plugin._applyClickListen();

                // placeholder element to put in where the content was
                plugin.$placeholder = $('<div/>').attr({ 'class': plugin.options.placeholderElementClass, 'style': 'display:none;', 'name': plugin.options.panelName });

                // get target element
                plugin.$target = $(plugin.options.loadTarget);
            },

            _flyoutSetup: function() {
                var plugin = this,
                    applyBtn = plugin.options.desktopApplyElement,
                    cancBtn = plugin.options.desktopCancelBtn;

                plugin.$target = $(plugin.options.loadTarget);
                if (plugin.options.applyOnChange !== null)
                    plugin.$applyOnChangeTarget = $(plugin.options.applyOnChange);

                plugin.$applyButton = $(applyBtn);
                plugin.$cancelButton = $(cancBtn);

                plugin._cancelClickListen();
                plugin._applyClickListen(true);
            },

            _moveContent: function() {
                var plugin = this;

                // insert placeholder after target & remove target from dom
                plugin.$target.after(plugin.$placeholder);
                plugin.$target.detach();

                // insert the content into the panel
                plugin.$panelBody.append(plugin.$target.show());
            },

            _putContentBack: function() {
                var plugin = this;

                plugin.$target.detach().insertAfter(plugin.$placeholder);
                plugin.$placeholder.detach();
            },

            // setup browser history back listener
            _historyListen: function() {
                var plugin = this;

                //add popstate listener to window
                $(window).on('popstate.isolatr.' + plugin.options.panelName, function(event) {
                    event.preventDefault();
                    if (event.originalEvent.state && event.originalEvent.state.isolatr
                        && event.originalEvent.state.isolatr.panelName === plugin.options.panelName) {

                        switch (event.originalEvent.state.isolatr.action) {
                        case "open":
                            plugin.open(false);
                            break;
                        case "close":
                            plugin.close(event.originalEvent.state.isolatr.scrollTo);
                        }
                    }
                });
            },

            // setup listeners 
            _clickListen: function() {
                var plugin = this;

                if (plugin.options.openOnInit) {
                    plugin.open(true);
                    plugin.options.openOnInit = false;
                }
                plugin.$element.on('mouseup.isolatr.' + plugin.options.panelName, function(e) {
                    e.preventDefault();
                    e.stopPropagation();
                    plugin.open(true);
                });
                if (plugin.$applyOnChangeTarget != undefined) {
                    plugin.$applyOnChangeTarget.on('change', function(e) {
                        plugin.open(true);
                    });
                }
            },

            open: function(pushState) {
                // re-reference the plugin to avoid this conflicts
                var plugin = this,
                    pushIt = pushState || true;

                // exit if this is already open
                if (plugin.isOpen) return;

                if (plugin.currentContext === "MOBILE") {
                    if (pushIt) {
                        // save the main page to history
                        history.replaceState({ isolatr: { panelName: plugin.options.panelName, action: 'close', scrollTo: $(window).scrollTop() } }, '');
                        // add new history entry for the current panel
                        history.pushState({ isolatr: { panelName: plugin.options.panelName, action: 'open' } }, '');
                    }

                    // move content
                    this._moveContent();

                    // do the show/hide shizz
                    plugin.$main.hide();
                    plugin.$panel.show(0, function() {
                        // scroll to top of panel
                        window.scrollTo(0, 1);
                    });
                    // It's only a modal on mobile; we don't publish this event on desktop.
                    amplify.publish("modal/open");
                }
                //desktop
                else {
                    plugin._flyoutSetup();
                    plugin.$target.show();
                    if (plugin.options.closeOutsideTarget !== null) {
                        plugin.options.closeOutsideFn = function(event) {
                            var closeOutsideMe = $(plugin.options.closeOutsideTarget);
                            if ($(closeOutsideMe).has(event.target).length === 0) {
                                $(document).off('click touchstart', plugin.options.closeOutsideFn);
                                plugin.close();
                            }
                        }
                        $(document).on('click touchstart', plugin.options.closeOutsideFn);
                    }
                }
                // temporarily remove the open listener
                plugin.$element.off('mouseup.isolatr.' + plugin.options.panelName);

                // set the flag
                plugin.isOpen = true;

                // fire callback
                plugin._openCallback();
            },
            //TODO: add document level listener for multiple node event listening
            _applyClickListen: function(immediateApply) {
                var plugin = this;
                if (plugin.currentContext === "MOBILE") {
                    plugin.$applyButton.on('touchstart.isolatr.' + plugin.options.panelName + ' click.isolatr.' + plugin.options.panelName, function(event) {
                        event.preventDefault();
                        event.stopPropagation();
                        plugin.doApply = true;

                        if (plugin.currentContext === "MOBILE") {
                            history.go(-1);
                        } else {
                            plugin.close();
                        }
                    });
                } else {
                    plugin.$applyButton.off('readyToApply');
                    plugin.$applyButton.on('readyToApply', function(event) {
                        event.preventDefault();
                        event.stopPropagation();
                        plugin.doApply = true;

                        if (plugin.currentContext === "MOBILE") {
                            history.go(-1);
                        } else {
                            plugin.close();
                        }
                    });
                }
            },

            _cancelClickListen: function() {
                var plugin = this;
                plugin.$cancelButton.on('touchstart.isolatr.' + plugin.options.panelName + ' click.isolatr.' + plugin.options.panelName, function(event) {
                    event.preventDefault();
                    plugin.doApply = false;
                    if (plugin.currentContext === "MOBILE") {
                        history.go(-1);
                    }
                });
            },

            close: function(scrollTo) {
                // re-reference the plugin to avoid this conflicts
                var plugin = this;

                // exit if this isn't already open
                if (!plugin.isOpen) return;

                if (plugin.currentContext === "MOBILE") {
                    plugin.$main.show();

                    plugin.$panel.hide(0, function() {
                        // put the content back where it belongs
                        plugin._putContentBack();

                        //hide element that shouldn't be in the main panel
                        $(plugin.options.hideElement).hide();

                        // turn the open click listener back on
                        plugin._clickListen();

                        // fire the correct callback
                        if (plugin.doApply) {
                            plugin._apply();
                            plugin.doApply = false;
                        } else {
                            plugin._cancel();
                        }

                        // scroll back to where we were - android needs a little extra time
                        _.delay(function() {
                            $(window).scrollTop(scrollTo);
                        }, 100);
                        // It's only a modal on mobile; we don't publish this event on desktop.
                        amplify.publish("modal/close");
                    });
                }
                //desktop
                else {
                    $(plugin.options.hideElement).hide();

                    // turn the open click listener back on
                    plugin._clickListen();

                    // fire the correct callback
                    if (plugin.doApply) {
                        plugin._apply();
                        plugin.doApply = false;
                    } else {
                        plugin._cancel();
                    }
                }
                plugin.isOpen = false;
            },

            _cancel: function() {
                var plugin = this;
                plugin.options.cancelCallback = helpers.getFuncFromString(plugin.options.cancelCallback);
                // give the plugin time to close before executing callback
                _.delay(plugin.options.cancelCallback, 100);
            },

            _apply: function(internalCallback) {
                var plugin = this;
                plugin.options.applyCallback = helpers.getFuncFromString(plugin.options.applyCallback);

                // give the plugin time to close before executing callback
                _.delay(plugin.options.applyCallback, 100);
                if (internalCallback != undefined)
                    internalCallback();
            },

            _openCallback: function() {
                var plugin = this;
                plugin.options.openCallback = helpers.getFuncFromString(plugin.options.openCallback);
                plugin.options.openCallback();
            },

            _reset: function() {
                var plugin = this;

                if (plugin.currentContext === "MOBILE") {
                    // if the panel is open, close it
                    if (plugin.isOpen) {
                        history.go(-1);
                    }
                    // remove listeners
                    $(window).off('popstate.isolatr.' + plugin.options.panelName);
                    plugin.$element.off('mouseup.isolatr.' + plugin.options.panelName);
                    plugin.$applyButton.off('touchstart.isolatr.' + plugin.options.panelName + ' click.isolatr.' + plugin.options.panelName);
                    plugin.$cancelButton.off('touchstart.isolatr.' + plugin.options.panelName + ' click.isolatr.' + plugin.options.panelName);
                    // destroy the panel
                    plugin.$panel.remove();
                } else {
                    plugin.$element.off('mouseup.isolatr.' + plugin.options.panelName);
                    if (plugin.$applyButton != undefined) {
                        plugin.$applyButton.off('readyToApply');
                        plugin.$cancelButton.off('touchstart.isolatr.' + plugin.options.panelName + ' click.isolatr.' + plugin.options.panelName);
                    }
                }
            },

            destroy: function() {
                var plugin = this;

                plugin._reset();

                // remove the data
                plugin.$element.removeData('plugin_' + pluginName);
            }
        };

        // Turn this into a jquery plugin & prevent multiple instantiations
        $.fn[pluginName] = function(options) {
            return this.each(function() {
                if (!$.data(this, 'plugin_' + pluginName)) {
                    $.data(this, 'plugin_' + pluginName,
                        new Isolatr(this, options));
                }
            });
        }

    })(jQuery, window, document);
});

